import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { CalendardetailsComponent } from '../calendardetails/calendardetails.component';

@Component({
  selector: 'app-listcalendar',
  templateUrl: './listcalendar.page.html',
  styleUrls: ['./listcalendar.page.scss'],
})
export class ListcalendarPage implements OnInit {

  constructor(private modalController: ModalController) { }

  ngOnInit() {
  }
  async showDetail() {
    const modal = await this.modalController.create({
      component: CalendardetailsComponent,
      cssClass: 'fullScreenModal'
    });
    return await modal.present();
  }
}
